// PromptSection Component
import React from 'react';
import { Box, Typography, TextField, Button } from '@mui/material';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';

export const PromptSection = ({
  prompt,
  setPrompt,
  enhancedPrompt,
  handleCopyPrompt,
  systemPrompt,
  setSystemPrompt,
}) => {
  return (
    <Box sx={{ display: 'flex', flexDirection: { xs: 'column', md: 'row' }, width: '100%', gap: 2, mt: 2 }}>
      {/* Step 1: What you want to do? */}
      <Box sx={{ flex: 1 }}>
        <Typography variant="h5" component="h2" gutterBottom>
          Step 1: What you want to do?
        </Typography>
        <Box sx={{ position: 'relative', width: '100%' }}>
          <TextField
            label="Enter your prompt"
            variant="outlined"
            value={prompt}
            onChange={(e) => setPrompt(e.target.value)}
            sx={{ width: '100%', mb: 2 }}
            multiline
            rows={4}
          />
        </Box>

        {/* System Prompt Textbox */}
        <Box sx={{ position: 'relative', width: '100%', mt: 2 }}>
          <TextField
            label="System Prompt"
            variant="outlined"
            value={systemPrompt}
            onChange={(e) => setSystemPrompt(e.target.value)}
            sx={{ width: '100%', mb: 2 }}
            multiline
            rows={4}
          />
        </Box>
      </Box>

      {/* Step 2: Copy to AI */}
      <Box sx={{ flex: 1 }}>
        <Typography variant="h5" component="h2" gutterBottom>
          Step 2: Copy to AI
        </Typography>
        <Box sx={{ position: 'relative', width: '100%' }}>
          <TextField
            label="Enhanced Prompt"
            variant="outlined"
            value={enhancedPrompt}
            sx={{ width: '100%', mb: 2 }}
            multiline
            rows={8}
            InputProps={{
              readOnly: true,
            }}
          />
          <Button
            variant="contained"
            color="primary"
            startIcon={<ContentCopyIcon />}
            onClick={handleCopyPrompt}
            sx={{ width: 'auto' }}
          >
            Copy and paste this to AI Chat Textbox
          </Button>
        </Box>
      </Box>
    </Box>
  );
};