import React, { useState, useEffect } from 'react';
import Layout from '../../components/Layout';
import { Box, Typography, Button, IconButton, Dialog, DialogTitle, DialogContent, DialogContentText, DialogActions } from '@mui/material';
import VisibilityIcon from '@mui/icons-material/Visibility';
import LinkIcon from '@mui/icons-material/Link'; // Import the Link icon
import { SnackbarAlert } from '../../components/placeholder/SnackbarAlert';
import { CodeEditor } from '../../components/placeholder/CodeEditor';
import { AppOutput } from '../../components/placeholder/AppOutput';
import { AIChat } from '../../components/placeholder/AIChat';
import { PromptSection } from '../../components/placeholder/PromptSection';
import { basicHTMLApp } from '../../helpers/git';
import LoginModal from '../../components/LoginModal';
import Parse from '../../vendor/Parse';

const AppPage = ({ placeholder }) => { // Accept placeholder as a prop
  const [prompt, setPrompt] = useState('');
  const [enhancedPrompt, setEnhancedPrompt] = useState('');
  const [code, setCode] = useState(basicHTMLApp);
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState('');
  const [snackbarSeverity, setSnackbarSeverity] = useState('info');
  const [systemPrompt, setSystemPrompt] = useState(
    typeof window !== 'undefined' ? sessionStorage.getItem(`systemPrompt_${placeholder}`) || 'as a frontend developer, create a web application for the above in a single html file' : 'as a frontend developer, create a web application for the above in a single html file'
  );
  const [showCodeEditor, setShowCodeEditor] = useState(true);
  const [showAppOutput, setShowAppOutput] = useState(true);
  const [showAI, setShowAI] = useState(true);
  const [appName, setAppName] = useState('MyApp');
  const [loginModalOpen, setLoginModalOpen] = useState(false);
  const [user, setUser] = useState(null);
  const [isAppDeployed, setIsAppDeployed] = useState(false); // State to track if the app is deployed
  const [deployModalOpen, setDeployModalOpen] = useState(false); // State to control the deploy modal
  const [isDeploying, setIsDeploying] = useState(false); // State to track if deployment is in progress
  const [deployStatusMessage, setDeployStatusMessage] = useState(''); // State to store the deployment status message

  useEffect(() => {
    if (typeof window !== 'undefined') {
      const savedCode = sessionStorage.getItem(`code_${placeholder}`);
      if (savedCode) {
        setCode(savedCode);
      }
    }
  }, [placeholder]);

  useEffect(() => {
    if (typeof window !== 'undefined') {
      sessionStorage.setItem(`code_${placeholder}`, code);
    }
  }, [code, placeholder]);

  useEffect(() => {
    if (typeof window !== 'undefined') {
      sessionStorage.setItem(`systemPrompt_${placeholder}`, systemPrompt);
    }
  }, [systemPrompt, placeholder]);

  useEffect(() => {
    setEnhancedPrompt(`${code}\n\n${prompt}\n\n${systemPrompt}`);
  }, [prompt, code, systemPrompt]);

  useEffect(() => {
    // Check if the user is already logged in
    const currentUser = Parse.User.current();
    if (currentUser) {
      setUser(currentUser);
    }
  }, []);

  useEffect(() => {
    // Check the status of the deployed app
    const checkAppStatus = async () => {
      try {
        const response = await fetch(`https://gallery.appify.website/${placeholder}/index.html`);
        if (response.ok) {
          setIsAppDeployed(true);
          setDeployStatusMessage(''); // Clear the status message once the app is deployed
          clearInterval(intervalId); // Stop the interval once the app is deployed
        }
      } catch (error) {
        console.error('Error checking app status:', error);
      }
    };

    let intervalId;
    if (isDeploying) {
      intervalId = setInterval(checkAppStatus, 30000); // Check every 30 seconds
    }

    return () => clearInterval(intervalId); // Cleanup interval on component unmount
  }, [isDeploying, placeholder]);

  const handleCopyPrompt = () => {
    navigator.clipboard.writeText(enhancedPrompt).then(() => {
      setSnackbarMessage('Prompt copied to clipboard!');
      setSnackbarSeverity('success');
      setSnackbarOpen(true);
    });
  };

  const handlePromptChange = (e) => {
    const value = e.target.value;
    setPrompt(value);
  };

  const handleDeploy = async () => {
    try {
      // Call the Parse Cloud Function with appname, code, and placeholder
      const result = await Parse.Cloud.run('deployApp', {
        appname: appName,
        code,
        placeholder, // Include placeholder from the URL
      });

      // Handle the result
      setSnackbarMessage('App deployed successfully!');
      setSnackbarSeverity('success');
      setSnackbarOpen(true);
      setIsAppDeployed(true); // Set the app as deployed
      setDeployStatusMessage(''); // Clear the deployment status message
      console.log('Deployment result:', result);
    } catch (error) {
      // Handle errors
      setSnackbarMessage('Failed to deploy app. Please try again.');
      setSnackbarSeverity('error');
      setSnackbarOpen(true);
      console.error('Deployment error:', error);
    }
  };

  const handleLoginSuccess = () => {
    const currentUser = Parse.User.current();
    setUser(currentUser);
  };

  const handleDeployClick = () => {
    // Open the deploy modal
    setDeployModalOpen(true);
  };

  const handleDeployConfirm = () => {
    // Close the modal and proceed with deployment
    setDeployModalOpen(false);
    setIsDeploying(true); // Set deployment in progress
    setDeployStatusMessage('The link will appear in around few minutes later...'); // Set the status message
    handleDeploy();
  };

  return (
    <Layout deployButton={<Button variant="contained" onClick={handleDeployClick}>Deploy</Button>} appName={appName}>
      <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center', minHeight: '80vh', textAlign: 'center' }}>
        <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
          <Typography variant="h3" component="h1" gutterBottom>
            {appName}
          </Typography>
          {isAppDeployed && (
            <IconButton 
              color="primary" 
              aria-label="app deployed" 
              component="a" 
              href={`https://gallery.appify.website/${placeholder}/index.html`} 
              target="_blank"
            >
              <LinkIcon />
            </IconButton>
          )}
        </Box>

        {/* Deployment Status Message */}
        {deployStatusMessage && (
          <Typography variant="body1" color="textSecondary">
            {deployStatusMessage}
          </Typography>
        )}

        {/* Toggle Buttons */}
        <Box sx={{ display: 'flex', gap: 2, mb: 2 }}>
          {!showCodeEditor && (
            <Button variant="outlined" onClick={() => setShowCodeEditor(true)} startIcon={<VisibilityIcon />}>
              Show Code Editor
            </Button>
          )}
          {!showAppOutput && (
            <Button variant="outlined" onClick={() => setShowAppOutput(true)} startIcon={<VisibilityIcon />}>
              Show App Output
            </Button>
          )}
          {!showAI && (
            <Button variant="outlined" onClick={() => setShowAI(true)} startIcon={<VisibilityIcon />}>
              Show AI
            </Button>
          )}
        </Box>

        {/* Three-View Layout */}
        <Box sx={{ display: 'flex', flexDirection: { xs: 'column', md: 'row' }, width: '100%', gap: 2, mt: 2 }}>
          {showCodeEditor && <CodeEditor code={code} setCode={setCode} showCodeEditor={showCodeEditor} setShowCodeEditor={setShowCodeEditor} />}
          {showAppOutput && <AppOutput code={code} showAppOutput={showAppOutput} setShowAppOutput={setShowAppOutput} />}
          {showAI && <AIChat showAI={showAI} setShowAI={setShowAI} />}
        </Box>

        {/* Prompt Section */}
        <PromptSection
          prompt={prompt}
          setPrompt={setPrompt}
          enhancedPrompt={enhancedPrompt}
          handleCopyPrompt={handleCopyPrompt}
          systemPrompt={systemPrompt}
          setSystemPrompt={setSystemPrompt}
        />
      </Box>

      {/* Login Modal */}
      <LoginModal open={loginModalOpen} onClose={() => setLoginModalOpen(false)} onLoginSuccess={handleLoginSuccess} />

      {/* Deploy Confirmation Modal */}
      <Dialog open={deployModalOpen} onClose={() => setDeployModalOpen(false)}>
        <DialogTitle>Bookmark This App</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Before deploying, we recommend bookmarking this app to avoid losing it. Press <strong>Ctrl+D</strong> (Windows/Linux) or <strong>Cmd+D</strong> (Mac) to bookmark this page. Once Deployed, there will be a link beside the app name.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setDeployModalOpen(false)}>Cancel</Button>
          <Button onClick={handleDeployConfirm} color="primary" variant="contained">
            Deploy
          </Button>
        </DialogActions>
      </Dialog>

      {/* Snackbar */}
      <SnackbarAlert open={snackbarOpen} message={snackbarMessage} severity={snackbarSeverity} onClose={() => setSnackbarOpen(false)} />
    </Layout>
  );
};

export default AppPage;